<template>
    <div>
        <div style="margin-top: 4px; width: 500px;  ">
            <el-input  @keyup.enter.native="seachEnter" placeholder="请输入QQ号" v-model="myInput" class="input-with-select" >

                <el-button slot="append" icon="el-icon-search" @click="mySearch"></el-button>
            </el-input>
        </div>

        <p style="color: white">查询进度：{{ list1_jindu }}/{{  list1.length}}</p>
        <template>
            <el-table :data="list1_data" style="width: 100%" :row-class-name="QQname">
                <el-table-column width="100px" prop="id" label="接口" />
                <el-table-column  label="照片" >
                    <template slot-scope="scope">
                        <el-image style="height: 50px;width: 50px;" :lazy='true'  class="bookImg" :src="scope.row.img"></el-image>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="昵称" />
                <el-table-column prop="phone" label="手机" />
                <el-table-column prop="email" label="邮箱" />
                <el-table-column prop="other" label="其他"/>
            </el-table>
        </template>




    </div>

</template>

<script>
    export default {

        data(){
            return{
                tongbu:0,
                myInput:"",

                list1:[
                    {id:1,url:"https://zy.xywlapi.cc/qqapi?qq="},
                    {id:2,url:"https://api.vvhan.com/api/qq?qq="},
                    {id:3,url:"https://zy.xywlapi.cc/qqlol?qq="},
                    {id:4,url:"/qq/qq.php?qq="},

                ],
                list1_data:[
                    // {id:0,img:"",name:"",phone:"",email:"",other:""}
                ],
                list1_jindu:0,



            }
        },
        methods: {
            mySearch(){
                if(this.myInput != ""){
                    let t = 1;
                    if(this.mySelect != null)
                        t = this.mySelect;
                    if(t == 1){
                        this.search1();
                    }
                }
            },

            //QQ
            async search1(){
                this.$message('开始执行，请稍等...');
                if(this.tongbu == 1){
                    this.$message('正在执行中...');
                    return ;
                }
                this.tongbu = 1;

                this.list1_jindu = 0;
                this.list1_data = [];

                console.log(this.list1);
                for(var i=0;i<this.list1.length;i++){

                    let ee = this.list1[i];
                    const {data:res} = await this.$axios.get(ee.url+this.myInput);
                    console.log(i+"--------"+res);
                    let mes = {img:"",name:"",phone:"",email:"",other:""};
                    mes.id = ee.id;

                    if(ee.id == 1){
                        mes.phone = res.phone;
                        mes.other = "归属地："+ res.phonediqu
                    }
                    if(ee.id == 2){
                        mes.img = res.imgurl;
                        mes.name = res.name;
                    }
                    if(ee.id == 3){
                        mes.other = "LOL昵称："+ res.name + "LOL大区："+ res.daqu;
                    }
                    if(ee.id == 4){
                        mes.img = res.imgurl;
                        mes.name = res.name;
                    }

                    this.list1_jindu = i+1;
                    this.list1_data.push(mes);
                    this.tongbu = 0;
                    this.$message("接口"+i+"查询结束！");
                }
            },
            seachEnter(e) {
                let keyCode = window.event ? e.keyCode : e.which;
                if (keyCode == 13) {
                    this.mySearch(); //搜索按钮的回调
                }
            },
        }
    }
</script>

<style scoped>
    .el-card:hover {
        transform: scale(1);
    }
</style>