<template>
    <div style="overflow-y:scroll;overflow-x:hidden;height:100%; ">

        <el-card class="box-card-my" style="margin-right: 100px">
            <div v-for=" it in infoList" :key="it.id" class="text item">
                <p1>{{ it.info }}</p1>
            </div>
        </el-card>


<!--        <div v-for=" it in infoList" style="width: 100%;height: auto; background-color: white;display: flex;justify-content: center;align-items: Center; ">-->
<!--            <p1>{{ it.info }}</p1>-->
<!--        </div>-->

        <el-col :span="5" v-for=" it in class_list" :key="it.id" style="margin: 10px">
            <div @click="myClick(it.id)">
                <el-card shadow="hover" style="background: rgba(10,10,10, 0.4);">
                    <i class="el-icon-s-home"/>
                    <p1 style="color: white">&#12288{{ it.name }}</p1><br/><br/>
                    <p1 style="font-size : 1px; color: #409EFF"> &#12288 &#12288 {{ it.text }} </p1>
                </el-card>
            </div>
        </el-col>


    </div>
</template>

<script>
    export default {

        data(){
            return{
                tongbu:0,
                infoList:[
                    {info:"",}
                ],
                class_list:[

                    {id:1,name:"历史上的今天", text:"查询历史", path: "http://res.abeim.cn/api-history_today",},
                    {id:2,name:"网易云热评", text:"热评", path: "http://res.abeim.cn/api-music_163_comment",},
                    {id:3,name:"一句情话 ", text:"情话", path: "http://res.abeim.cn/api-text_sweet?export=json",},
                    {id:4,name:"一句段子", text:"段子", path: "http://res.abeim.cn/api-text_wu?export=json",},
                    {id:5,name:"一句经典语录", text:"语录", path: "http://res.abeim.cn/api-text_yiyan?export=json",},
                    {id:6,name:"每日星座", text:"星座", path: "https://api.vvhan.com/api/horoscope?type=scorpio&time=today",},
                    {id:7,name:"随机热评", text:"热评", path: "https://api.vvhan.com/api/reping",},
                    {id:8,name:"随机一句话", text:"一句话", path: "https://api.vvhan.com/api/ian",},
                    {id:9,name:"随机动漫", text:"动漫", path: "https://api.vvhan.com/api/ian?cl=ac",},
                    {id:10,name:"随机文学", text:"文学", path: "https://api.vvhan.com/api/ian?cl=wx",},
                    {id:11,name:"随机笑话", text:"笑话", path: "https://api.vvhan.com/api/joke?type=json",},
                    {id:12,name:"随机情话", text:"情话", path: "https://api.vvhan.com/api/love?type=json",},
                    {id:13,name:"随机骚话", text:"骚话", path: "https://api.vvhan.com/api/sao?type=json",},
                    {id:14,name:"舔狗文字", text:"舔狗", path: "https://v.api.aa1.cn/api/tiangou/index.php",},
                ]
            }
        },
        methods: {
            async myClick(id){
                this.$message('开始执行，请稍等...');
                if(this.tongbu == 1){
                    this.$message('正在执行中...');
                    return ;
                }
                this.tongbu = 1;

                this.infoList = [];
                let myurl = this.class_list[id-1].path;
                const {data:res} = await this.$axios.get(myurl);
                console.log(res);

                if (id == 1){
                    for(let i=0;i<res.data.length;i++){
                        let temp = res.data[i].time +" ----- " + res.data[i].data;
                        this.infoList.push({'info':temp})
                    }
                }
                if(id == 2){this.infoList.push({'info':res.data.content}) }
                if(id == 3){this.infoList.push({'info':res.content}) }
                if(id == 4){this.infoList.push({'info':res.content}) }
                if(id == 5){this.infoList.push({'info':res.content}) }
                if(id == 6){
                    this.infoList.push({'info':res.data.title})
                    this.infoList.push({'info':res.data.time})
                    this.infoList.push({'info': "幸运颜色：" + res.data.luckycolor})
                    this.infoList.push({'info': "幸运数字：" + res.data.luckynumber})
                    this.infoList.push({'info': "幸运星座：" + res.data.luckyconstellation})
                    this.infoList.push({'info': res.data.fortunetext.all})
                    this.infoList.push({'info': res.data.fortunetext.love})
                    this.infoList.push({'info': res.data.fortunetext.work})
                    this.infoList.push({'info': res.data.fortunetext.money})
                    this.infoList.push({'info': res.data.fortunetext.health})
                }
                if(id == 7){this.infoList.push({'info':res.data.content}) }
                if(id == 8){this.infoList.push({'info':res}) }
                if(id == 9){this.infoList.push({'info':res}) }
                if(id == 10){this.infoList.push({'info':res}) }
                if(id == 11){this.infoList.push({'info':res.joke}) }
                if(id == 12){this.infoList.push({'info':res.ishan}) }
                if(id == 13){this.infoList.push({'info':res.ishan}) }
                if(id == 14){this.infoList.push({'info':res}) }

                this.$message('执行结束');
                this.tongbu = 0;
            }
        }
    }
</script>

<style scoped>
    .box-card-my:hover {
        transform: scale(1);
    }
</style>