<template>
    <div style="overflow-y:scroll;overflow-x:hidden;height:100%; ">


        <el-card class="box-card-my" style="margin-right: 100px;background-color: rgba(0,0,0,0)">
            <div v-for=" it in infoList" :key="it" class="text item">
                <div  style="width: 100%;height: auto;  text-align: center; ">
                    <div>
                        <p1 style="color: white" >{{ it.info }}</p1><br/>
                    </div>

                    <div style="margin: 10px">
                        <img :src="it.url" style="width: 500px;height: auto;" />
                    </div>
                </div>

            </div>
        </el-card>


        <!--        <div v-for=" it in infoList" style="width: 100%;height: auto; background-color: white;display: flex;justify-content: center;align-items: Center; ">-->
        <!--            <p1>{{ it.info }}</p1>-->
        <!--        </div>-->

        <el-col :span="5" v-for=" it in class_list" :key="it.id" style="margin: 10px">
            <div @click="myClick(it.id)">
                <el-card shadow="hover" style="background: rgba(10,10,10, 0.4);">
                    <i class="el-icon-s-home"/>
                    <p1 style="color: white">&#12288{{ it.name }}</p1><br/><br/>
                    <p1 style="font-size : 1px; color: #409EFF"> &#12288 &#12288 {{ it.text }} </p1>
                </el-card>
            </div>
        </el-col>


    </div>
</template>

<script>
    export default {

        data(){
            return{
                tongbu:0,
                infoList:[],
                class_list:[


                    {id:1,name:"网易云照片", text:"网易云", path: "http://res.abeim.cn/api-music_163_comment",},
                    {id:2,name:"获取Bing壁纸", text:"壁纸", path: "http://res.abeim.cn/api-bing_img",},
                    {id:3,name:"随机图片", text:"图片", path: "https://v.api.aa1.cn/api/api-meiribizhi/api.php",},
                    {id:4,name:"随机图片", text:"图片", path: "https://v.api.aa1.cn/api/api-gqsh/index.php?wpon=json",},
                    {id:5,name:"随机图片", text:"图片", path: "https://v.api.aa1.cn/api/api-tplist/go.php/api/picture",},

                    {id:6,name:"随机图片", text:"图片", path: "/xiaowai/api/api.php?return=json",},




                    //跨域
                    // {id:6,name:"随机图片", text:"图片", path: "https://v.api.aa1.cn/api/pc-girl_bz/index.php?wpon=ro38d57y8rhuwur3788y3rd",},
                    // {id:5,name:"随机图片", text:"图片", path: "https://v.api.aa1.cn/api/api-fj-2/index.php?aa1=yuantu",},
                    // {id:5,name:"随机图片", text:"图片", path: "https://v.api.aa1.cn/api/api-fj-1/index.php?aa1=yuantu",},

                ]
            }
        },
        methods: {
            async myClick(id){
                this.$message('开始执行，请稍等...');
                if(this.tongbu == 1){
                    this.$message('正在执行中...');
                    return ;
                }
                this.tongbu = 1;

                this.infoList = [];
                let myurl = this.class_list[id-1].path;

                const {data:res} = await this.$axios.get(myurl);
                console.log(res);




                if(id == 1){this.infoList.push({info:res.data.nickname,url:res.data.avatar}) }
                if(id == 2){this.infoList.push({info:res.img_copy,url:res.img_url}) }
                if(id == 3){
                    for(let i=0;i<10;i++){
                        this.infoList.push({info:res.data[i].utag,url:res.data[i].url});
                    }
                }
                if(id == 4){this.infoList.push({info:"随机图片",url:res.img})}
                if(id == 5){
                    for(let i=0;i<20;i++){
                        this.infoList.push({info:res.data[i].setname,url:res.data[i].cover});
                    }
                }
                if(id == 6){
                    this.infoList.push({info:"随机图片",url:res.imgurl});
                }

                console.log(this.infoList);

                this.$message('执行结束');
                this.tongbu = 0;
            }
        }
    }
</script>

<style scoped>
    .box-card-my:hover {
        transform: scale(1);
    }
</style>