<template>
    <div style="overflow-y:scroll;overflow-x:hidden;height:100%; ">




                <div  style="width: 100%;height: auto;  text-align: center; ">
                    <div>
                        <p1 style="color: white" >{{ this.music.name }}</p1><br/>
                    </div>

                    <div style="margin: 10px">
                        <img :src="this.music.img" style="width: 100px;height: 100px;" @click="play"/>
                    </div>


                </div>

        <el-col :span="5" v-for=" it in class_list" :key="it.id" style="margin: 10px">
            <div @click="myClick(it.id)">
                <el-card shadow="hover" style="background: rgba(10,10,10, 0.4);">
                    <i class="el-icon-s-home"/>
                    <p1 style="color: white">&#12288{{ it.name }}</p1><br/><br/>
                    <p1 style="font-size : 1px; color: #409EFF"> &#12288 &#12288 {{ it.text }} </p1>
                </el-card>
            </div>
        </el-col>


    </div>
</template>

<script>
    export default {

        data(){
            return{
                tongbu:0,
                zhuangtai:0,
                audio: new Audio(),
                music: {
                    name:"没有正在播放的歌曲",
                    img:"https://s3.bmp.ovh/imgs/2022/12/31/eb09d35ea2f9763c.png",
                    url:""
                },
                class_list:[

                    {id:1,name:"随机音乐", text:"随机", path: "https://api.vvhan.com/api/rand.music?type=json&sort=热歌榜",},

                ]
            }
        },
        methods: {
            async myClick(id){
                this.$message('开始执行，请稍等...');
                if(this.tongbu == 1){
                    this.$message('正在执行中...');
                    return ;
                }
                this.tongbu = 1;

                this.music.name = "正在寻找歌曲请稍等..."
                this.zhuangtai =0;

                let myurl = this.class_list[id-1].path;
                const {data:res} = await this.$axios.get(myurl);
                console.log(res);

                if (id == 1){
                    this.music.name = res.info.name + "--------------(点击图片：播放/暂停)";
                    this.music.img = res.info.picUrl;
                    this.music.url = res.info.mp3url;
                }
                console.log(this.music.url);
                this.audio.src = this.music.url;

                this.audio.play();
                this.zhuangtai = 1;

                this.$message('执行结束');
                this.tongbu = 0;

            },
            // 放弃
            async play(){
                if(this.zhuangtai == 0){
                    await this.audio.play();
                    this.zhuangtai = 1;
                }else {
                    await  this.audio.pause();
                    this.zhuangtai = 0;
                }


            }

        }
    }
</script>

<style scoped>
    .box-card-my:hover {
        transform: scale(1);
    }
</style>